<template>
  <div class="addvisitBox">
    <!--  -->
    <header>
      <a @click="backpage">
        <img src="../assets/backIcon.png">
      </a>
      <p></p>
      <!-- <router-link tag="span" to="/Workbench"></router-link> -->
    </header>
    <section class="formData ignore">
      <div class="hasdata">
        <label>
          <i></i>:
        </label>
        <p>{{name}}</p>
      </div>
      <div class="selectcheck">
        <label>
          <i>*</i>:
        </label>
        <el-select v-model="businessValue" placeholder="">
          <el-option
            :key="business.uuid"
            :label="business.name"
            :value="business.name"
            v-for="business in businessList"
          ></el-option>
        </el-select>
      </div>
      <div class="selectcheck">
        <label>
          <i>*</i>:
        </label>
        <el-select v-model="typeValue" placeholder="">
          <el-option
            :key="index"
            :label="type.name"
            :value="type.name"
            v-for="(type,index) in typeList"
          ></el-option>
        </el-select>
      </div>
      <div class="selectcheck">
        <label>
          <i>*</i>:
        </label>
        <el-select v-model="goalValue" placeholder="">
          <el-option
            :key="index"
            :label="goal.name"
            :value="goal.name"
            v-for="(goal,index) in goalList"
          ></el-option>
        </el-select>
      </div>
      <div>
        <label>
          <i>*</i>:
        </label>
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 4}"
          v-model="content"
          placeholder="14"
        ></el-input>
      </div>
      <div class="visitSrc">
        <label>
          <i>*</i>:
        </label>
        <div>
          <span @click="visitrc"></span>
          <ul class="enclosure">
            <!-- -->
            <li v-for="(urlSrc,index) in visitImg" :key="index">
              <img :src="urlSrc">
              <!-- <div>
                <span>{{urlSrc.del}}</span>
              </div>-->
            </li>
          </ul>
        </div>
      </div>
      <el-row class="submitBtn borderNone">
        <el-button type="primary" @click="submitform">{{submitname}}</el-button>
      </el-row>
    </section>
  </div>
</template>
<style lang="less" scoped>
@import url("../style/basic/basics.less");
@import url("../style/workbench.less");
@import url("../style/Addvisit.less");
</style>

<script>
import axios from "axios";
// import wx from "weixin-js-sdk";
import wx from "weixin-js-sdk";
export default {
  data() {
    return {
      name: this.$route.params.name,
      businessList: [],
      businessValue: "",
      typeList: [{ name: "" }, { name: "" }, { name: "APP" }],
      typeValue: "",
      goalList: [{ name: "" }, { name: "" }, { name: "" }],
      goalValue: "",
      content: "",
      visitImg: [],
      ioslocId: [], //iosid base64,
      submitname:""
    };
  },
  watch: {},
  methods: {
    backpage() {
      this.$router.go(-1); //
    },
    async getbusiness() {
      const url = this.httpsBasic.httpsBasic + "business/selectBusinessList";
      const params = new URLSearchParams();
      params.append("token", window.localStorage.getItem("operatingToken"));
      const { data } = await axios.get(`${url}?${params.toString()}`);
      if (data.code == 1001) {
        this.businessList = data.data;
        // console.log(data)
      } else if (data.code == 1010) {
        this.$alert("", "", {
          confirmButtonText: "",
          callback: action => {
            this.$router.push({ name: "Login" });
          }
        });
      } else {
        this.$message.error(data.msg);
      }
    },
    getwx: function() {
      let wechaturl = window.location.href.split("#")[0];
      if (window.wechaturl !== undefined) {
        wechaturl = window.wechaturl;
      }
      const url = this.httpsBasic.httpsBasicWx + "wechat/jssdk";
      // alert(wxUrl)
      axios
        .post(url, {
          url: wechaturl
        })
        .then(function(res) {
          wx.config({
            debug: false, // ,
            appId: res.data.data.appId, // corpid
            timestamp: res.data.data.timestamp, // 
            nonceStr: res.data.data.nonceStr, // 
            signature: res.data.data.signature, // 1
            jsApiList: res.data.data.jsApiList // JSJS2
          });
        });
    },
    visitrc() {
      // alert("aaa")
      let _this = this;
      //
      this.getwx();
      wx.ready(function() {
        wx.chooseImage({
          count: 9, // 9
          sizeType: ["compressed"], // 
          sourceType: ["album", "camera"], // 
          success: function(res) {
            let localIds = res.localIds; // IDlocalIdimgsrc
            _this.ioslocId = [];
            _this.visitImg = [];
            _this.localimg(localIds);
            _this.upimg(localIds);
          }
        });
      });
    },
    localimg(localIds) {
      // alert(JSON.stringify(bosImg));
      // alert(bosImg.length);
      let _this = this;
      // 
      wx.ready(function() {
        for (let i = 0; i < localIds.length; i++) {
          var xt = navigator.userAgent;
          if (window.__wxjs_is_wkwebview) {
            // alert("" + window.__wxjs_is_wkwebview)
            // alert(localIds[i]);
            wx.getLocalImgData({
              localId: localIds[i].toString(), // localID
              success: function(res) {
                //  alert("" + JSON.stringify(res));
                // localDatabase64img
                _this.visitImg.push(res.localData);
              }
            });
          } else {
            _this.visitImg.push(localIds[i]);
          }
        }
      });
    },
    upimg(localIds) {
      let _this = this;
      let localId = localIds.pop();
      // alert("wx"+ wx)
      wx.ready(function() {
        wx.uploadImage({
          localId: localId.toString(),
          success: function(res) {
            _this.ioslocId.push(res.serverId);
            if (localIds.length > 0) {
              setTimeout(function() {
                _this.upimg(localIds); //
              }, 200);
            }
          },
          fail: function(res) {
            alert(JSON.stringify(res));
          }
        });
      });
    },
    submitform() {
      // console.log(this.businessValue)
      const url = this.httpsBasic.httpsBasic + "eguard/visit";
      const _this = this;
    if(this.submitname==""){
      this.submitname="...";
        axios
        .post(url, {
          e_uuid: this.$route.params.e_uuid,
          business: this.businessValue,
          type: this.typeValue,
          desitination: this.goalValue,
          content: this.content,
          imgs: this.ioslocId,
          token: window.localStorage.getItem("operatingToken")
        })
        .then(function(res) {
            // alert(JSON.stringify(res));
          if(res.data.code==1001){
               _this.$message.success("");
                _this.submitname=""
              setTimeout(() => {
                window.scrollTo(0, 0);
                _this.$router.push({ name: "Visit" });
              }, 2000);
          }else{
            _this.$message.error(res.data.msg);
              _this.submitname=""
          }
        })
        .catch(function(error) {
           _this.$message.error(error);
           _this.submitname=""
        });
    }
    }
  },
  mounted() {
    this.getbusiness();
  }
};
</script>

<style>
</style>

