<template>
  <div class="fontsize">
    <!--  -->
    <header>
      <a @click="backpage">
        <img src="../assets/backIcon.png">
      </a>
      <p></p>
    </header>
    <!--      -->
    <section class="container">
      <hr>
      <router-link to="/changepassword">
        <p></p>
        <img src="../assets/user/jiantouzuo.png" alt="arrow">
      </router-link>
      <a>
        <p></p>
        <span>1.2.2</span>
      </a>
      <a class="quit" @click="quitHref"></a>
    </section>
  </div>
</template>

<style scoped lang="less" >
@import url("../style/basic/basics.less");
@import url("../style/Setting.less");
</style>
<script>
import axios from "axios";
export default {
  methods: {
    backpage() {
      this.$router.go(-1); //
    },
    quitHref(){
      let url = this.httpsBasic.httpsBasic + "logout";
      let _this=this
      axios.post(url,{
        token:window.localStorage.getItem("operatingToken")
      }).then(function(response){
        if(response.data.code==1001){
          _this.$router.push("/")
        }else{
          _this.$message.error(response.data.msg)
        }
      }).catch(function(error){
          _this.$message.error(response.data.msg)
      })
    }
  }
};
</script>


