<script>
import axios from "axios";
import wx from "weixin-js-sdk";
export default {
  data() {
    return {
      tel: this.$route.params.tel,
      name: this.$route.params.name,
      identity: "",
      shopValue: [],
      identityFrount: require("../assets/interview/identityFrount.png"),
      identitySide: require("../assets/interview/identitySide.png"),
      bankcard: require("../assets/interview/yinhangqia.png"),
      addbusiness: [
        {
          addtitle: "",
          addicon: require("../assets/add.png"),
          b_uuid: "",
          c_uuids: [],
          ifshops: false
        }
      ],
      shops: [],

      businessList: [],
      bankNum: "",
      id_img_n: "",
      id_img_p: "",
      bank_card_img: "",
      submitname: "",
      loading: false,
      options4:[]
    };
  },
  watch: {
    b_uuid: function() {
      console.log(this.addbusiness);
      // this.addbusiness.businessValue != ""
      //   ? (this.addbusiness.ifshops = true && this.getshops())
      //   : (this.addbusiness.ifshops = false);
    },
    shopValue: function() {
      console.log(this.shopValue);
    }
  },
  methods: {
    blurShow(){
    this.$refs.chooseKpi.blur() 
    },
    backpage: function() {
      this.$router.go(-1); //
    },
    focusShow() {
      // console.log("aa");
    },
    checkboxClick(e) {
      console.log(e);
    },
    getUUID(e, i) {
      // console.log(e)
      if (e != "") {
        this.addbusiness[i].ifshops = true;
        this.businessValue = e;
        this.getshops();
      }
    },
    submitform: function() {
      if (this.submitname == "") {
        this.submitname = "...";
        let _this = this;
        if (this.bankNum != "" && this.identity != "" && this.id_img_p!="" && this.id_img_n!="" && this.bank_card_img!="" && this.addbusiness[0]['c_uuids'].length>0 ) {
          if(this.identity.length==18){
             const url = this.httpsBasic.httpsBasic + "eguard/entry";
          axios
            .post(url, {
              e_uuid: this.$route.params.e_uuid,
              id_num: this.identity,
              token: window.localStorage.getItem("operatingToken"),
              location: JSON.stringify(this.addbusiness),
              bank_card_num: this.bankNum,
              id_img_p: this.id_img_p,
              id_img_n: this.id_img_n,
              bank_card_img: this.bank_card_img
            })
            .then(function(res) {
              // console.log(res)
              _this.$message.success("");
              _this.submitname = "";
              setTimeout(() => {
                window.scrollTo(0, 0);
                // _this.$router.push({ name: "Resourcelib" });
                _this.$router.go(-1); //
              }, 2000);
              // alert(JSON.stringify(res))
            })
            .catch(function(error) {
              _this.$message.error(error);
              _this.submitname = "";
            });
          }else{
             _this.$message.warning("");
             _this.submitname = "";
          }
         
        } else {
          _this.$message.warning("");
          _this.submitname = "";
        }
      }

      // console.log(this.tel);
    },
    async getbusiness() {
      const url = this.httpsBasic.httpsBasic + "business/selectBusinessList";
      const params = new URLSearchParams();
      params.append("token", window.localStorage.getItem("operatingToken"));
      const { data } = await axios.get(`${url}?${params.toString()}`);
      if (data.code == 1001) {
        // for (let i = 0; i < this.addbusiness.length; i++) {
        //   this.addbusiness[i].businessList = data.data;
        // }
        this.businessList = data.data;
      } else if (data.code == 1010) {
        this.$alert("", "", {
          confirmButtonText: "",
          callback: action => {
            this.$router.push({ name: "Login" });
          }
        });
      } else {
        this.$message.error(data.msg);
      }
    },
    async getshops() {
      const url =
        this.httpsBasic.httpsBasic + "business/selectBusinessChildList";
      const params = new URLSearchParams();
      params.append("token", window.localStorage.getItem("operatingToken"));
      params.append("uuid", this.businessValue);
      const { data } = await axios.get(`${url}?${params.toString()}`);
      if (data.code == 1001) {
        // for (let i = 0; i < this.addbusiness.length; i++) {
        //   this.addbusiness[i].shops = data.data;
        // }
        this.shops = data.data;
        this.restaurants = data.data;
      } else if (data.code == 1010) {
        this.$alert("", "", {
          confirmButtonText: "",
          callback: action => {
            this.$router.push({ name: "Login" });
          }
        });
      } else {
        this.$message.error(data.msg);
      }
    },
    addmodel: function(add, index) {
      this.getbusiness();
      console.log(add);
      if (add == "") {
        this.addbusiness.push({
          addtitle: "",
          addicon: require("../assets/delete.png"),
          start_time: "",
          b_uuid: "",
          c_uuids: [],
          businessList: [],
          ifshops: false,
          shops: []
        });
      } else {
        this.addbusiness.splice(index, 1);
      }
    },
    shopsToggle: function(uuid, index) {
      // console.log(index);
      // console.log(uuid);
    },
    getwx: function() {
      let wechaturl = window.location.href.split("#")[0];
      if (window.wechaturl !== undefined) {
        wechaturl = window.wechaturl;
      }
      const url = this.httpsBasic.httpsBasicWx + "wechat/jssdk";
      // alert(wxUrl)
      axios
        .post(url, {
          url: wechaturl
        })
        .then(function(res) {
          wx.config({
            debug: false, // ,
            appId: res.data.data.appId, // corpid
            timestamp: res.data.data.timestamp, // 
            nonceStr: res.data.data.nonceStr, // 
            signature: res.data.data.signature, // 1
            jsApiList: res.data.data.jsApiList // JSJS2
          });
        });
    },
    async identityFrountSrc() {
      const _this = this;
      wx.ready(function() {
        wx.chooseImage({
          count: 1, // 9
          sizeType: ["original", "compressed"], // 
          sourceType: ["album", "camera"], // 
          success: function(res) {
            let bosImg = res.localIds[0].toString(); // IDlocalIdimgsrc
            wx.uploadImage({
              localId: bosImg, // IDchooseImage
              isShowProgressTips: 1, // 1
              success: function(res) {
                const result = res.serverId; // ID
                _this.id_img_p = result;
                // 
                var xt = navigator.userAgent;
                if (xt.indexOf("OS") > -1) {
                  wx.getLocalImgData({
                    localId: bosImg, // localID
                    success: function(res) {
                      // localDatabase64img
                      _this.identityFrount = res.localData;
                    }
                  });
                } else {
                  _this.identityFrount = result;
                }
              }
            });
          }
        });
      });
    },
    async identitySideSrc() {
      const _this = this;
      wx.ready(function() {
        wx.chooseImage({
          count: 1, // 9
          sizeType: ["original", "compressed"], // 
          sourceType: ["album", "camera"], // 
          success: function(res) {
            let bosImg = res.localIds[0].toString(); // IDlocalIdimgsrc
            wx.uploadImage({
              localId: bosImg, // IDchooseImage
              isShowProgressTips: 1, // 1
              success: function(res) {
                const result = res.serverId; // ID
                _this.id_img_n = result;
                // 
                var xt = navigator.userAgent;
                if (xt.indexOf("OS") > -1) {
                  wx.getLocalImgData({
                    localId: bosImg, // localID
                    success: function(res) {
                      // localDatabase64img
                      _this.identitySide = res.localData;
                    }
                  });
                } else {
                  _this.identitySide = result;
                }
              }
            });
          }
        });
      });
    },
    async bankcardSrc() {
      const _this = this;
      wx.ready(function() {
        wx.chooseImage({
          count: 1, // 9
          sizeType: ["original", "compressed"], // 
          sourceType: ["album", "camera"], // 
          success: function(res) {
            let bosImg = res.localIds[0].toString(); // IDlocalIdimgsrc
            wx.uploadImage({
              localId: bosImg, // IDchooseImage
              isShowProgressTips: 1, // 1
              success: function(res) {
                const result = res.serverId; // ID
                _this.bank_card_img = result;
                // 
                var xt = navigator.userAgent;
                if (xt.indexOf("OS") > -1) {
                  wx.getLocalImgData({
                    localId: bosImg, // localID
                    success: function(res) {
                      // localDatabase64img
                      _this.bankcard = res.localData;
                    }
                  });
                } else {
                  _this.bankcard = result;
                }
              }
            });
          }
        });
      });
    }
  },
  mounted() {
    this.getbusiness();
    this.getwx(); //JSSDK
    // console.log(this.businessList);
  }
};
</script>

