<template>
  <div class="addvisitBox visitDetails">
    <!--  -->
    <header>
      <a @click="backpage">
        <img src="../assets/backIcon.png">
      </a>
      <p></p>
      <!-- <router-link tag="span" to="/Workbench"></router-link> -->
    </header>
    <section class="formData ignore">
      <div class="hasdata">
        <label>
          <i></i>:
        </label>
        <p>{{name}}</p>
      </div>
      <div class="selectcheck">
        <label>
          <i></i>:
        </label>
        <p>{{businessValue}}</p>
      </div>
      <div class="selectcheck">
        <label>
          <i></i>:
        </label>
        <p>{{typeValue}}</p>
      </div>
      <div class="selectcheck">
        <label>
          <i></i>:
        </label>
        <p>{{goalValue}}</p>
      </div>
      <div>
        <label>
          <i></i>:
        </label>
        <p>{{content}}</p>
      </div>
      <div class="visitSrc">
        <label>
          <i></i>:
        </label>
        <ul class="enclosure">
          <!-- -->
          <li v-for="(urlSrc,index) in visitImg" :key="index">
            <img :src="urlSrc"  @click="imgDetails(urlSrc)">
            <!-- <div>
                <span>{{urlSrc.del}}</span>
            </div>-->
          </li>
        </ul>
      </div>
    </section>
  </div>
</template>
<style lang="less" scoped>
@import url("../style/basic/basics.less");
@import url("../style/workbench.less");
@import url("../style/Addvisit.less");
</style>
<style lang="less">
.visitDetails{
    .formData>div{
         align-items: center;
    }
    p{
        font-size:30px;
        color:#556677;
        line-height:32px;
    }
}
</style>

<script>
import axios from "axios";
// import wx from "weixin-js-sdk";
import wx from "weixin-js-sdk";
export default {
  data() {
    return {
      name: this.$route.params.name,
      businessList: [],
      businessValue: "",
      typeList: [{ name: "" }, { name: "" }, { name: "APP" }],
      typeValue: "",
      goalList: [{ name: "" }, { name: "" }, { name: "" }],
      goalValue: "",
      content: "",
      visitImg: [],
      ioslocId: [] //iosid base64
    };
  },
  watch: {},
  methods: {
    backpage() {
      this.$router.go(-1); //
    },
    async getData() {
      const url = this.httpsBasic.httpsBasic + "visit/selectInfo";
      const params = new URLSearchParams();
      params.append("token", window.localStorage.getItem("operatingToken"));
      // console.log(this.$route.params.uuid)
      params.append("uuid", this.$route.params.uuid);
      const { data } = await axios.get(`${url}?${params.toString()}`);
      if (data.code == 1001) {
        this.typeValue = data.data.type; //
        this.goalValue = data.data.desitination; //
        this.content = data.data.content; //
        this.visitImg = data.data.imgs; //
        this.businessValue = data.data.business;
      } else if (data.code == 1010) {
        this.$alert("", "", {
          confirmButtonText: "",
          callback: action => {
            this.$router.push({ name: "Login" });
          }
        });
      } else {
        this.$message.error(data.msg);
      }
      // console.log(data);
    },
      imgDetails(pic) {
      // console.log(this.imgList);
       WeixinJSBridge.invoke("imagePreview", {
          urls: this.visitImg,
          current: pic
        });
    },
    getwx: function() {
      let wechaturl = window.location.href.split("#")[0];
      if (window.wechaturl !== undefined) {
        wechaturl = window.wechaturl;
      }
      const url = this.httpsBasic.httpsBasicWx + "wechat/jssdk";
      // alert(wxUrl)
      axios
        .post(url, {
          url: wechaturl
        })
        .then(function(res) {
          wx.config({
            debug: false, // ,
            appId: res.data.data.appId, // corpid
            timestamp: res.data.data.timestamp, // 
            nonceStr: res.data.data.nonceStr, // 
            signature: res.data.data.signature, // 1
            jsApiList: res.data.data.jsApiList // JSJS2
          });
        });
    },
  },
  mounted() {
    this.getData();
     this.getwx();
  }
};
</script>

